"use client"

import { useState, useEffect } from 'react'
import { useRouter } from 'next/navigation'
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { Badge } from "@/components/ui/badge"
import { Separator } from "@/components/ui/separator"

interface Service {
  id: string
  title: string
  description: string
  price: number
  category: string
  status: 'active' | 'inactive'
}

interface User {
  name: string
  email: string
  role: string
}

export default function WorkerDashboard() {
  const [user, setUser] = useState<User | null>(null)
  const [services, setServices] = useState<Service[]>([])
  const [showAddForm, setShowAddForm] = useState(false)
  const [newService, setNewService] = useState({
    title: '',
    description: '',
    price: '',
    category: ''
  })
  const [error, setError] = useState('')
  const [success, setSuccess] = useState('')
  const router = useRouter()

  useEffect(() => {
    // Check authentication
    const userData = localStorage.getItem('user')
    if (!userData) {
      router.push('/auth/login')
      return
    }

    const parsedUser = JSON.parse(userData)
    if (parsedUser.role !== 'worker') {
      router.push('/dashboard/customer')
      return
    }

    setUser(parsedUser)

    // Load existing services from localStorage
    const savedServices = localStorage.getItem(`services_${parsedUser.email}`)
    if (savedServices) {
      setServices(JSON.parse(savedServices))
    }
  }, [router])

  const handleAddService = (e: React.FormEvent) => {
    e.preventDefault()
    setError('')
    setSuccess('')

    if (!newService.title || !newService.description || !newService.price || !newService.category) {
      setError('Please fill in all fields')
      return
    }

    if (isNaN(Number(newService.price)) || Number(newService.price) <= 0) {
      setError('Please enter a valid price')
      return
    }

    const service: Service = {
      id: Date.now().toString(),
      title: newService.title,
      description: newService.description,
      price: Number(newService.price),
      category: newService.category,
      status: 'active'
    }

    const updatedServices = [...services, service]
    setServices(updatedServices)
    
    // Save to localStorage
    if (user) {
      localStorage.setItem(`services_${user.email}`, JSON.stringify(updatedServices))
    }

    setNewService({ title: '', description: '', price: '', category: '' })
    setShowAddForm(false)
    setSuccess('Service added successfully!')
  }

  const handleDeleteService = (serviceId: string) => {
    const updatedServices = services.filter(s => s.id !== serviceId)
    setServices(updatedServices)
    
    if (user) {
      localStorage.setItem(`services_${user.email}`, JSON.stringify(updatedServices))
    }
    setSuccess('Service deleted successfully!')
  }

  const handleLogout = () => {
    localStorage.removeItem('user')
    router.push('/')
  }

  if (!user) {
    return <div className="container mx-auto px-4 py-8">Loading...</div>
  }

  return (
    <div className="container mx-auto px-4 py-8">
      {/* Header */}
      <div className="flex justify-between items-center mb-8">
        <div>
          <h1 className="text-3xl font-bold">Worker Dashboard</h1>
          <p className="text-muted-foreground">Welcome back, {user.name}</p>
        </div>
        <Button variant="outline" onClick={handleLogout}>
          Logout
        </Button>
      </div>

      {/* Success/Error Messages */}
      {error && (
        <Alert variant="destructive" className="mb-6">
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      )}
      {success && (
        <Alert className="mb-6">
          <AlertDescription>{success}</AlertDescription>
        </Alert>
      )}

      {/* Stats Cards */}
      <div className="grid md:grid-cols-3 gap-6 mb-8">
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">Active Services</CardTitle>
          </CardHeader>
          <CardContent>
            <p className="text-3xl font-bold">{services.filter(s => s.status === 'active').length}</p>
          </CardContent>
        </Card>
        
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">Total Earnings</CardTitle>
          </CardHeader>
          <CardContent>
            <p className="text-3xl font-bold">$0</p>
            <p className="text-sm text-muted-foreground">This month</p>
          </CardContent>
        </Card>
        
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">Bookings</CardTitle>
          </CardHeader>
          <CardContent>
            <p className="text-3xl font-bold">0</p>
            <p className="text-sm text-muted-foreground">Pending requests</p>
          </CardContent>
        </Card>
      </div>

      {/* Services Section */}
      <Card>
        <CardHeader>
          <div className="flex justify-between items-center">
            <div>
              <CardTitle>My Services</CardTitle>
              <CardDescription>Manage your service offerings</CardDescription>
            </div>
            <Button onClick={() => setShowAddForm(!showAddForm)}>
              {showAddForm ? 'Cancel' : 'Add Service'}
            </Button>
          </div>
        </CardHeader>
        <CardContent>
          {/* Add Service Form */}
          {showAddForm && (
            <div className="mb-6 p-4 border rounded-lg">
              <h3 className="text-lg font-semibold mb-4">Add New Service</h3>
              <form onSubmit={handleAddService} className="space-y-4">
                <div className="grid md:grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="title">Service Title</Label>
                    <Input
                      id="title"
                      placeholder="e.g., House Cleaning"
                      value={newService.title}
                      onChange={(e) => setNewService(prev => ({ ...prev, title: e.target.value }))}
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="category">Category</Label>
                    <Input
                      id="category"
                      placeholder="e.g., Home Services"
                      value={newService.category}
                      onChange={(e) => setNewService(prev => ({ ...prev, category: e.target.value }))}
                    />
                  </div>
                </div>
                
                <div className="space-y-2">
                  <Label htmlFor="description">Description</Label>
                  <Textarea
                    id="description"
                    placeholder="Describe your service in detail..."
                    value={newService.description}
                    onChange={(e) => setNewService(prev => ({ ...prev, description: e.target.value }))}
                    rows={3}
                  />
                </div>
                
                <div className="space-y-2">
                  <Label htmlFor="price">Price ($)</Label>
                  <Input
                    id="price"
                    type="number"
                    placeholder="0.00"
                    value={newService.price}
                    onChange={(e) => setNewService(prev => ({ ...prev, price: e.target.value }))}
                  />
                </div>
                
                <Button type="submit">Add Service</Button>
              </form>
            </div>
          )}

          {/* Services List */}
          {services.length === 0 ? (
            <div className="text-center py-8">
              <p className="text-muted-foreground">No services added yet.</p>
              <p className="text-sm text-muted-foreground mt-2">
                Add your first service to start receiving bookings!
              </p>
            </div>
          ) : (
            <div className="space-y-4">
              {services.map((service) => (
                <div key={service.id} className="border rounded-lg p-4">
                  <div className="flex justify-between items-start">
                    <div className="flex-1">
                      <div className="flex items-center gap-2 mb-2">
                        <h3 className="text-lg font-semibold">{service.title}</h3>
                        <Badge variant={service.status === 'active' ? 'default' : 'secondary'}>
                          {service.status}
                        </Badge>
                      </div>
                      <p className="text-muted-foreground mb-2">{service.description}</p>
                      <div className="flex items-center gap-4 text-sm">
                        <span className="font-medium">${service.price}</span>
                        <span className="text-muted-foreground">{service.category}</span>
                      </div>
                    </div>
                    <Button 
                      variant="destructive" 
                      size="sm"
                      onClick={() => handleDeleteService(service.id)}
                    >
                      Delete
                    </Button>
                  </div>
                </div>
              ))}
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  )
}
